.flexLogo {
	overflow: auto;
	display: -ms-flexbox;
	display: flex;

	-ms-flex-direction: row;

	flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;

	padding-bottom: 30px;
}

.logo {
	width: 171px;
	-webkit-transition: all 0.6s;
	-o-transition: all 0.6s;
	transition: all 0.6s;
	margin: 0px;
	margin-right: 40px;
	margin-top: 20px;
	margin-bottom: 20px;
	
}

.logo:hover {
	-webkit-box-shadow: 30px 20px 42px -13px #a4a3a3;
	        box-shadow: 30px 20px 42px -13px #a4a3a3;
}

.title {
	font-weight: 800;
	font-size: 2em;
	color: #154985;
}

@media only screen and (max-width:920px) {
	.flexLogo {
		-ms-flex-pack: space-evenly;
		    justify-content: space-evenly;

		
	}

	.logo{
		margin: 0px;
		margin: 20px;
	}

	.title{
		text-align: center;
	}
}
@media only screen and (max-width: 445px){
	.logo{
		width: 150px;
	}
}

@media only screen and (max-width: 400px){
	.logo{
		width: 100px;
	}
	}


