.mainImg {
	width: 500px;
	display: block;
	margin: auto;
	margin-top: -281px;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.triangleContainer {
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
    margin: auto;
    height: 500px; 
	float: left;
	
	margin-top: 65px;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}
   

.triangle1 {
	width: 116px;
	margin-top: 145px;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.subTriangleContainer {
	text-align: right;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
	display: block;
}
.triangle2 {
	width: 116px;
	margin-top: -118px;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
	position: absolute;
	right: -2px;
	top: 80px;
}

.triangle3 {
	width: 50px;

	margin-top: -142px;
	display: block;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.triangleText1 {
	position: absolute;
	left: 14px;
    top: -103px;
    color: #154985;
    font-weight: bold;
    cursor: default;
    
}

.triangleText2 {
	position: absolute;
	left: 427px;
    top: -1px;
    color:#d8d8d8;
    font-weight: bold;
    cursor: default;
}




@media only screen and (max-width: 842px){
	.triangleContainer{
		margin-top: 0px;
	}
	
}




















































































