	-o-transition: all 0.3s;
	transition: all 0.3s;
	border-radius: 100px;
	border-style: none;
	margin-right: 26px;
	margin-top: 26PX;
	margin-bottom: 26px;
}



.title {
	font-weight: 800;
	font-size: 2em;
	color: white;
}

@media only screen and (max-width: 920px) {
	.flexLogo {
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
