.hrLogo {
	width: 80px;
	float: left;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.flexContainer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	flex-direction: row;
	-ms-flex-align: center;
	align-items: center;
	-ms-flex-pack: justify;
	justify-content: space-between;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.flexContainer2 {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	flex-direction: column;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.companyName {
	font-weight: 900;
	font-size: 3em;
	color: #154985;
	padding: 0;
	margin: 0;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.slogan {
	font-size: 1.4em;
	padding: 0;
	margin: 10px 0px 0px 0px;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.superFlexContainer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	flex-direction: column;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

@media only screen and (max-width: 870px) {
	.flexContainer2 {
		transform: scale(0.8);
		-moz-transform: scale(0.8);
		-ms-transform: scale(0.8);
		-o-transform: scale(0.8);
		-webkit-transform: scale(0.8);
	}

	.triangles {
		-webkit-transform: scale(0.8);
		    -ms-transform: scale(0.8);
		        transform: scale(0.8);
	}
}

@media only screen and (max-width: 842px) {
	.flexContainer {
		-ms-flex-pack: space-evenly;
		justify-content: space-evenly;
		-ms-flex-direction: column;
		flex-direction: column;
		margin-top: 100px;
	}
	.flexContainer2 {
		width: 500px;
	}

	.triangles {
		margin-top: -150px;
	}
}

@media only screen and (max-width: 836px) {
	.flexContainer2 {
		-webkit-transform: scale(0.7);
		    -ms-transform: scale(0.7);
		        transform: scale(0.7);
	}
	.triangles {
		-webkit-transform: scale(0.7);
		    -ms-transform: scale(0.7);
		        transform: scale(0.7);
		margin-top: -170px;
	}
}

@media only screen and (max-width: 376px) {
	.flexContainer2 {
		transform: scale(0.6);
		-webkit-transform: scale(0.6);
		-moz-transform: scale(0.6);
		-ms-transform: scale(0.6);
		-o-transform: scale(0.6);
	}
	.triangles {
		transform: scale(0.6);
		-webkit-transform: scale(0.6);
		-moz-transform: scale(0.6);
		-ms-transform: scale(0.6);
		-o-transform: scale(0.6);
		margin-top: -200px;
	}
}

@media only screen and (max-width: 640px) {
	.hrLogo {
		float: none;
		margin: auto;
		display: -ms-flexbox;
		display: flex;
	}
}

@media only screen and (max-width: 320px) {
	.triangles {
		-webkit-transform: scale(0.5);
		    -ms-transform: scale(0.5);
		        transform: scale(0.5);
		margin-top: -220px;
	}
	.flexContainer2 {
		transform: scale(0.5);
		-webkit-transform: scale(0.5);
		-moz-transform: scale(0.5);
		-ms-transform: scale(0.5);
		-o-transform: scale(0.5);
	}

	.slogan {
		font-size: 1.3em;
	}
}



