/* Color Scheme
    #d8d8d8
    #132344
    #154985
*/

.defaultContainer{
    margin:0;
    padding: 0;
    border-radius: 0 0 30px 30px;
    -webkit-border-radius: 0 0 30px 30px;
    -moz-border-radius: 0 0 30px 30px;
    -ms-border-radius: 0 0 30px 30px;
    -o-border-radius: 0 0 30px 30px;
}
.subDefaultContainer{
    max-width: 1040px;
    padding: 2%;
    margin: auto;
    overflow: auto;
    
}


